.sidebar-open {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	background-color: rgba(108, 117, 125, 0.5);
	width: 8rem;
	margin-left: 0rem;
	height: 100vh;
}
.sidebar-close {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	background-color: rgba(108, 117, 125, 0.5);
	width: 3rem;
	height: 100vh;
}

.sidebar-btn {
	padding:0rem;
	background: rgba(108, 117, 125,0);
}

.sidebar-icon {
	height: 20px;
	width: 20px;
}


.sidebar-section {
	color :#ffc107;
	padding: 0;
	background-color: rgba(108, 117, 125, 0);
}

.sidebar-section.selected {
	padding: 0;
	background-color: rgba(108, 117, 125, 1);
}

.sidebar-signout-btn-close {
 margin-left: 0.2rem;
 margin-bottom: 0.5rem;
 width: 2.5rem;
}


.sidebar-humburger-stick {
	background: #ffc107;
	margin-top: 0.3rem;
	margin-left: 0.5rem;
	margin-bottom: 0.3rem;
	border-radius: 1rem;
	height: 0.3rem;
	width: 2rem;
}
.user {
	margin: 1rem 2rem;
}

.sidebar-signout-btn {
	margin: 1rem;
}


@media screen and (max-width: 900px) {
	.sidebar-open {
		font-size: 0.75rem;
		height: 100vh;
	}
	.sidebar-close {
		height: 100vh;
	}
	
	.sidebar-btn {
		font-size: 0.75rem;
		padding:0rem;
		background: rgba(108, 117, 125,0);
	}
	

	
	.sidebar-section {
		font-size: 0.75rem;
		color :#ffc107;
		padding: 0;
		background-color: rgba(108, 117, 125, 0);
	}
	
	.sidebar-section.selected {
		padding: 0;
		background-color: rgba(108, 117, 125, 1);
	}
	
	.sidebar-signout-btn-close {
		
	 margin-left: 0.2rem;
	 margin-bottom: 0.5rem;
	 width: 2.5rem;
	}
	
	
	
	.sidebar-signout-btn {
		font-size: 0.75rem;
		margin: 1rem;
	}

}