.trips-container {
	background-color: rgba(108, 117, 125, 0.5);
	border-radius: 1rem;
	width: 80rem ;
	height: 35rem;
	display: flex;
	margin-left: auto;
	margin-right: auto;
	align-items: center;
	}

	.trips-table {
		background-color: rgba(239, 239, 239, 1);
		border-radius: 1rem;
		border: solid 2px black;
		width: 75rem ;
		height: 30rem;
		display: flex;
		flex-direction: column;
		margin-left: auto;
		margin-right: auto;
		align-items: center;
		}

	
		@media screen and (max-width: 900px) {
			.trips-container {
				width: 40rem ;
				height: 20rem;
				}
			
				.trips-table {
					width: 35rem ;
					height: 15rem;
					margin-left: auto;
					margin-right: auto;
					}
		
		}